import React, { useState, useEffect } from "react";
import {
  Input,
  Row,
  Col,
  Card,
  Form,
  TreeSelect,
  Upload,
  message,
  Button,
  Select,
  Checkbox,
  DatePicker,
} from "antd";
import { ImageSvg } from "assets/svg/icon";
import CustomIcon from "components/util-components/CustomIcon";
import { LoadingOutlined } from "@ant-design/icons";
import ExamApi from "api/exam-api";
import Flex from "components/shared-components/Flex";
import { useHistory } from "react-router-dom";
import { ExamCategoryApi } from "api/catalog-api";

const { Dragger } = Upload;
const { Option } = Select;

const rules = {
  content: [
    {
      required: true,
      message: "Nhp vo ni dung cu hi",
    },
  ],
};

const GeneralField = (props) => {
  const [item, setItem] = useState(null);
  const [submitLoading, setSubmitLoading] = useState(false);
  const [categories, setCategories] = useState(null);

  let history = useHistory();

  useEffect(() => {
    setItem(props.item);
    getQuestionCategories();
  }, [props.item]);

  const dateFormat = "DD-MM-YYYY HH:mm";

  const handleDateChange = (fieldName, value) => {
    const currentItem = Object.assign({}, item);
    currentItem[fieldName] = value;
    setItem({ ...currentItem });
    if (props.handleChange) {
      props.handleChange(currentItem);
    }
  };

  const handleChange = (event) => {
    event.persist();
    const currentItem = Object.assign({}, item);
    const targetValue =
      event.target.type === "checkbox"
        ? event.target.checked
        : event.target.value;
    currentItem[event.target.name] = targetValue;
    setItem({ ...currentItem });
    if (props.handleChange) {
      props.handleChange(currentItem);
    }
  };

  const handleSelectChange = (field, value) => {
    const currentItem = Object.assign({}, item);
    currentItem[field] = value;
    setItem({ ...currentItem });
    if (props.handleChange) {
      props.handleChange(currentItem);
    }
  };

  const onFinish = () => {
    setSubmitLoading(true);
    new ExamApi().save(item).then((response) => {
      setSubmitLoading(false);
      if (item.id === 0) {
        message.success(`To k thi thnh cng.`);
        history.push(`/lms/exams/edit/${response.data.id}/overview`);
      }
      if (item.id > 0) {
        message.success(`Cp nht k thi thnh cng.`);
      }
    });
  };

  const getQuestionCategories = () => {
    ExamCategoryApi.getAll().then((response) => {
      setCategories(response.data);
    });
  };

  const changeCategoryId = (value) => {
    const currentItem = Object.assign({}, item);
    currentItem.categoryId = parseInt(value);
    setItem({ ...currentItem });
    if (props.handleChange) {
      props.handleChange({ ...currentItem });
    }
  };

  return (
    item && (
      <>
        <Form layout="vertical" hideRequiredMark>
          <Row>
            <Col span={24}>
              <Flex justifyContent="between">
                <div className="mb-3">&nbsp;</div>
                <div className="mb-3">
                  <Button
                    type="primary"
                    onClick={() => onFinish()}
                    loading={submitLoading}
                  >
                    Cp nht
                  </Button>
                </div>
              </Flex>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col xs={24} sm={24} md={17}>
              <Card>
                <Row gutter={16}>
                  <Col xs={24} sm={24} md={24}>
                    <Form.Item label="Tn" rules={rules.content}>
                      <Input
                        name="name"
                        value={item.name}
                        defaultValue={item.name}
                        onChange={(event) => handleChange(event)}
                      />
                    </Form.Item>
                  </Col>
                </Row>
                <Row gutter={16}>
                  <Col xs={24} sm={24} md={24}>
                    <Form.Item label="M t" rules={rules.content}>
                      <Input.TextArea
                        name="content"
                        value={item.content}
                        defaultValue={item.content}
                        onChange={(event) => handleChange(event)}
                        rows={4}
                      />
                    </Form.Item>
                  </Col>
                </Row>
                <Row gutter={16}>
                  <Col xs={24} sm={24} md={12}>
                    <Form.Item label="Trng thi">
                      <Select
                        placeholder="Chn Trng thi K thi"
                        defaultValue={item.status}
                        value={item.status}
                        name="status"
                        onChange={(value) =>
                          handleSelectChange("status", value)
                        }
                      >
                        <Option value={0}>Chn</Option>
                        <Option value={1}>Chun b</Option>
                        <Option value={2}>ang din ra</Option>
                        <Option value={3}>Kt thc</Option>
                      </Select>
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={24} md={12}>
                    <Form.Item label="Hnh thc t chc">
                      <Select
                        placeholder="Chn Hnh thc t chc K thi"
                        defaultValue={item.examType}
                        value={item.examType}
                        name="type"
                        onChange={(value) =>
                          handleSelectChange("examType", value)
                        }
                      >
                        <Option value={0}>Chn</Option>
                        <Option value={1}>Luyn tp</Option>
                        <Option value={2}>Thi-Kim tra</Option>
                      </Select>
                    </Form.Item>
                  </Col>
                </Row>
                <Row gutter={16}>
                  <Col xs={24} sm={24} md={12}>
                    <Form.Item label="Hnh thc ng k">
                      <Select
                        placeholder="Chn Hnh thc ng k K thi"
                        defaultValue={item.registrationType}
                        value={item.registrationType}
                        name="registrationType"
                        onChange={(value) =>
                          handleSelectChange("registrationType", value)
                        }
                      >
                        <Option value={0}>Chn</Option>
                        <Option value={1}>Hn ch</Option>
                        <Option value={2}>Cng khai</Option>
                      </Select>
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={24} md={12}>
                    <Form.Item label="Hin th Kt qu sau khi thi">
                      <Select
                        placeholder="Cu hnh hin th Kt qu sau khi thi"
                        defaultValue={item.viewPermissionAfterFinish}
                        value={item.viewPermissionAfterFinish}
                        name="viewPermissionAfterFinish"
                        onChange={(value) =>
                          handleSelectChange("viewPermissionAfterFinish", value)
                        }
                      >
                        <Option value={0}>Chn</Option>
                        <Option value={1}>Ch xem im</Option>
                        <Option value={2}>Xem im v chi tit bi lm</Option>
                        <Option value={4}>
                          Khng xem im v chi tit bi lm
                        </Option>
                      </Select>
                    </Form.Item>
                  </Col>
                </Row>
                <Row gutter={16}>
                  <Col xs={24} sm={24} md={24}>
                    <Form.Item>
                      <Checkbox
                        checked={item.isSpecificDuration}
                        name="isSpecificDuration"
                        onChange={(event) => handleChange(event)}
                      >
                        Xc nh thi gian bt u/ kt thc
                      </Checkbox>
                    </Form.Item>
                  </Col>
                </Row>
                {item.isSpecificDuration && (
                  <Row gutter={16}>
                    <Col xs={24} sm={24} md={12}>
                      <Form.Item label="Thi gian bt u">
                        <DatePicker
                          showTime
                          className="w-100"
                          placeholder="Chn thi gian bt u"
                          // defaultValue={item.startDate}
                          onChange={(value, format) => {
                            handleDateChange("startDate", value);
                          }}
                          name="startDate"
                          format={dateFormat}
                        />
                      </Form.Item>
                    </Col>
                    <Col xs={24} sm={24} md={12}>
                      <Form.Item label="Thi gian kt thc">
                        <DatePicker
                          showTime
                          className="w-100"
                          placeholder="Chn thi gian kt thc"
                          // defaultValue={item.endDate}
                          onChange={(value, format) => {
                            handleDateChange("endDate", value);
                          }}
                          name="endDate"
                          format={dateFormat}
                        />
                      </Form.Item>
                    </Col>
                  </Row>
                )}
              </Card>
            </Col>
            <Col xs={24} sm={24} md={7}>
              <Card title="nh i din">
                <Dragger onChange={(e) => props.handleUploadChange(e)}>
                  <div>
                    {props.uploadLoading ? (
                      <div>
                        <LoadingOutlined className="font-size-xxl text-primary" />
                        <div className="mt-3">Uploading</div>
                      </div>
                    ) : (
                      <div>
                        <CustomIcon className="display-3" svg={ImageSvg} />
                        <p>Click or drag file to upload</p>
                      </div>
                    )}
                  </div>
                </Dragger>
              </Card>
              <Card title="Phn loi">
                <Form.Item name="category" label="Danh mc K thi">
                  {categories && (
                    <TreeSelect
                      style={{ width: "100%" }}
                      treeData={categories}
                      className="w-100"
                      dropdownStyle={{ maxHeight: 400, overflow: "auto" }}
                      placeholder="Chn Danh mc K thi"
                      allowClear
                      defaultValue={[props.item.categoryName]}
                      treeDefaultExpandAll
                      onChange={(value) => changeCategoryId(value)}
                    ></TreeSelect>
                  )}
                </Form.Item>
              </Card>
            </Col>
          </Row>
        </Form>
      </>
    )
  );
};

export default GeneralField;
