import React, { useState, useEffect } from "react";
import { Descriptions, Badge, Card } from "antd";
import ExamApi from "api/exam-api";
import moment from "moment";
import ExamStatChart from "./Chart";

const Overview = (props) => {
  const [item, setItem] = useState(null);
  const { param } = props;

  useEffect(() => {
    const { id } = param;
    const api = new ExamApi();
    api.getExamDashboard(parseInt(id)).then((response) => {
      setItem(response.data);
    });
  }, [param, props]);

  return (
    <>
      <Card>
        {item && (
          <Descriptions bordered>
            <Descriptions.Item label="Tn k thi">
              {item.name}
            </Descriptions.Item>
            <Descriptions.Item label="Hnh thc">
              {item.examTypeName}
            </Descriptions.Item>
            <Descriptions.Item label="Tham gia">
              {item.registrationTypeName}
            </Descriptions.Item>
            <Descriptions.Item label="Thi gian bt u">
              {moment(item.startDate).format("YYYY-MM-DD HH:mm:ss")}
            </Descriptions.Item>
            <Descriptions.Item label="Thi gian kt thc" span={2}>
              {moment(item.endDate).format("YYYY-MM-DD HH:mm:ss")}
            </Descriptions.Item>
            <Descriptions.Item label="Trng thi" span={3}>
              {item.status == 2 && <Badge status="processing" text="ang din ra" />}
              {item.status != 2 && <span>{item.statusName}</span>}
            </Descriptions.Item>
            <Descriptions.Item label="S mn thi">
              {item.totalExamItem}
            </Descriptions.Item>
            <Descriptions.Item label="S hc vin">
              {item.totalUser}
            </Descriptions.Item>
            <Descriptions.Item label="S lt lm bi">
              {item.totalAttempt}
            </Descriptions.Item>
            <Descriptions.Item label="Lch s">
              Ngy to: {moment(item.createdTime).format("YYYY-MM-DD HH:mm:ss")}
              <br />
              Ngy sa gn nht:{" "}
              {moment(item.lastModifiedTime).format("YYYY-MM-DD HH:mm:ss")}
            </Descriptions.Item>
          </Descriptions>
        )}
      </Card>
      <br />
      <Card title="Thng k">
        <ExamStatChart />
      </Card>
    </>
  );
};

export default Overview;
